import OpenAI from 'openai';
import { Assistant, Message } from '@/types';

const openai = new OpenAI({
  apiKey: process.env.NEXT_PUBLIC_OPENAI_API_KEY,
  dangerouslyAllowBrowser: true // Note: In production, you should use API routes
});

export async function createAssistant(config: Partial<Assistant>) {
  try {
    const assistant = await openai.beta.assistants.create({
      name: config.name,
      instructions: config.instructions,
      tools: (config.tools || []) as any,
      model: config.model || "gpt-4o",
      temperature: config.temperature,
      top_p: config.top_p
    });
    return assistant;
  } catch (error) {
    console.error('Error creating assistant:', error);
    throw error;
  }
}

export async function updateAssistant(assistantId: string, config: Partial<Assistant>) {
  try {
    const assistant = await openai.beta.assistants.update(
      assistantId,
      {
        name: config.name,
        instructions: config.instructions,
        tools: (config.tools || []) as any,
        model: config.model,
        temperature: config.temperature,
        top_p: config.top_p
      }
    );
    return assistant;
  } catch (error) {
    console.error('Error updating assistant:', error);
    throw error;
  }
} 